.elem-label {
    display: block;
    font-size: 12px;
    line-height: 15px;
    font-weight: 800;
    margin-bottom: 5px;
    position: relative;

    @media (max-width: $sm) {
        font-size: 10px;
        line-height: 16px;
    }

    &[required] {
        &:after {
            content: '*';
            color: $color-red;
            margin-left: 3px;
        }
    }
}
