.icon {
    transition: all 0.3s ease-in-out;

    &-user {
        width: 16px;
        height: 20px;
    }

    &-logo {
        width: 104px;
        height: 58px;

        @media (max-width: $lg) {
            height: 48px;
            width: 86px;
        }

        @media (max-width: $md) {
            height: 44px;
            width: 72px;
        }
    }

    &-search,
    &-consultant,
    &-ask,
    &-home2,
    &-filter,
    &-clear-filter,
    &-close,
    &-thumbs-up,
    &-thumbs-down {
        height: 24px;
        width: 24px;
    }

    &-filter {
        stroke: $color-base;

        @media (max-width: $lg) {
            height: 18px;
            width: 18px;
        }

        @media (max-width: $md) {
            height: 16px;
            width: 16px;
        }
    }

    &-table,
    &-list {
        height: 20px;
        width: 20px;
        fill: $color-gray-light;

        @media (max-width: $lg) {
            height: 14px;
            width: 14px;
        }

        .active & {
            fill: $color-base;
        }
    }

    &-24h,
    &-loyalty,
    &-favs,
    &-cart,
    &-all,
    &-user2 {
        height: 32px;
        width: 32px;

        @media (max-width: $lg) {
            height: 44px;
            width: 44px;
        }
    }

    &-show-more,
    &-vk,
    &-ok,
    &-youtube,
    &-adv-product,
    &-adv-product2,
    &-home,
    &-dacha,
    &-beauty,
    &-electronics,
    &-talisma,
    &-prevoslav,
    &-hobby,
    &-kids,
    &-cloth-shoes,
    &-back,
    &-arrow-back2,
    &-garbage-can,
    &-24h {
        height: 32px;
        width: 32px;
    }

    &-electronics,
    &-prevoslav,
    &-kids,
    &-hobby,
    &-adv-product {
        fill: $color-base;
        stroke: $color-base;
    }

    &-adv-product {
        stroke-width: 1;
    }

    &-delivery-free,
    &-delivery-rf,
    &-delivery-point,
    &-delivery-loyality {
        height: 76px;
        width: 76px;

        @media (max-width: $md) {
            height: 56px;
            width: 56px;
        }
    }

    &-mastercard {
        height: 20px;
        width: 34px;
    }

    &-visa {
        height: 17px;
        width: 49px;
    }

    &-mir {
        height: 15px;
        width: 55px;
    }

    &-halva {
        height: 16px;
        width: 55px;

        @media (max-width: $lg) {
            width: 65px;
        }
    }

    &-favs {
        fill: $color-base;
    }

    &-mail_illustration {
        height: 78px;
        width: 82px;

        @media (max-width: $lg) {
            height: 97px;
            width: 102px;
        }

        @media (max-width: $md) {
            height: 84px;
            width: 90px;
        }
    }

    &-arrow-back {
        height: 12px;
        width: 7px;
        stroke: $color-base;
    }

    &-cancel {
        height: 12px;
        width: 12px;
    }

    &-tv-404 {
        width: 320px;
        height: 299px;
    }

    &-404 {
        width: 379px;
        height: 142px;
    }
}
